import React, { Component } from 'react'
import { Card, Table, Tooltip, message, Button } from 'antd';
import { EyeOutlined } from '@ant-design/icons';
import AvatarStatus from 'components/shared-components/AvatarStatus';
import { getUserRequested } from '../../../../redux/actions'
import { connect } from 'react-redux';
import { Link } from 'react-router-dom/cjs/react-router-dom.min';
import { APP_PREFIX_PATH } from 'configs/AppConfig'



export class UserList extends Component {

	state = {
		userProfileVisible: false,
		selectedUser: null,
	}

	deleteUser = userId => {
		this.setState({
			users: this.props.users.list.filter(item => item.id !== userId),
		})
		message.success({ content: `Deleted user ${userId}`, duration: 2 });
	}


	componentDidMount() {
		this.props.getUserRequested()
	}
	
	render() {

 		const  users = this.props.users.list;

		
		const tableColumns = [
			{
				title: 'User',
				dataIndex: 'name',
				render: (_, record) => (
					<div className="d-flex">
						<AvatarStatus src={record.img} name={record.name} subTitle={record.email}/>
					</div>
				),
				sorter: {
					compare: (a, b) => {
						a = a.name.toLowerCase();
  						b = b.name.toLowerCase();
						return a > b ? -1 : b > a ? 1 : 0;
					},
				},
			},
			{
				title: 'User Name',
				dataIndex: 'username',
				render: username => (
					<span>{username}</span>
				),
				sorter: {
					compare: (a, b) => {
							a = a.name.toLowerCase();
							b = b.name.toLowerCase();
							return a > b ? -1 : b > a ? 1 : 0;
						},
					
				},
			},
			{
				title: 'City',
				dataIndex: 'address',
				render: address => (
					<span>{address.city}</span>
				),
				sorter: {
					compare: (a, b) => {
						a = a.name.toLowerCase();
  						b = b.name.toLowerCase();
						return a > b ? -1 : b > a ? 1 : 0;
					},
				},
			},
			{
				title: 'Website',
				dataIndex: 'website',
				render: website => (
					<span>{website}</span>
				),
				sorter: {
					compare: (a, b) => {
						a = a.name.toLowerCase();
  						b = b.name.toLowerCase();
						return a > b ? -1 : b > a ? 1 : 0
						},
				}
			},
			{
				title: '',
				dataIndex: 'actions',
				render: (_, elem) => (
					<div className="text-right">
						<Tooltip title="View">
						<Link to={`${APP_PREFIX_PATH}/clients/edit-profile/${elem.id}`} >
							<Button  type="primary" className="mr-2" icon={<EyeOutlined />} size="small" />
						</Link>
						</Tooltip>
					</div>
				)
			}
		];
		return (
			<Card bodyStyle={{'padding': '0px'}}>
				<Table columns={tableColumns} dataSource={users} rowKey='id' />
			</Card>
		)
	}
}

const mapStateToProps = (state) => {
	return {
		users : state.users
	}
}

const mapDispatchToProps = (dispatch) => {
	return {
		getUserRequested: () => dispatch(getUserRequested())
	}
}
export default connect(mapStateToProps, mapDispatchToProps)(UserList)
