import {
    GET_USERS_FETCH_REQUESTED, 
    GET_USERS_FETCH_SUCCEEDED, 
    GET_USERS_FETCH_FAILED, 
} from '../constants/Users' 

    export const getUserRequested = () => {
        return {
            type: GET_USERS_FETCH_REQUESTED
        }
    }

    export const getUserSucceeded = (users) => {
        return {
            type: GET_USERS_FETCH_SUCCEEDED,
            payload: users
        }
    }

    export const getUserFailed = () => {
        return {
            type: GET_USERS_FETCH_FAILED
        }
    }
